<template>
  <div class="ip-lookup-container">
    <h2>地址如下</h2>
    <div v-if="loading" class="loading">正在获取IP地址...</div>
    <div v-else-if="error" class="error">{{ error }}</div>
    <div v-else class="ip-details">
      <p>您的公网IP地址: <strong>{{ ipAddress }}</strong></p>
    </div>
    <button @click="fetchIPAddress" :disabled="loading">重新获取</button>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'IPLookupTool',
  data() {
    return {
      ipAddress: '',
      loading: false,
      error: null
    }
  },
  mounted() {
    this.fetchIPAddress()
  },
  methods: {
    async fetchIPAddress() {
      this.loading = true
      this.error = null
      try {
        const response = await axios.get('https://api.ipify.org?format=json')
        console.log(response.data)
        this.ipAddress = response.data.ip
      } catch (err) {
        this.error = '获取IP地址失败：' + err.message
      } finally {
        this.loading = false
      }
    }
  }
}
</script>

<style scoped>
.ip-lookup-container {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
}

.loading, .error {
  color: #666;
}

.ip-details {
  margin: 20px 0;
}

button {
  padding: 10px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}
</style>